<template>
  <div class="weather-page">
    <weather-page-header
      @initiate-search="searchWeatherData"
      :city-name="searchedRegion"
    />
    <v-skeleton-loader v-if="isLoading" type="card" />
    <weather-page-body
      v-else
      :weather-data="weatherData"
      :population-data="populationData"
    />
    <v-snackbar
      v-model="shouldShowToast"
      :timeout="toastTimeout"
      bottom
      right
      min-width="100"
      max-width="250"
    >
      {{ snackBarText }}
    </v-snackbar>
  </div>
</template>

<script lang="ts" setup>
import { computed, onMounted, ref, watch } from "vue";
import axios from "axios";

import {
  ApiResults,
  PopulationData,
  WeatherData,
} from "../../weather-app-server/ApiResultType";
import { BackendApi } from "../types/Enums";

import WeatherPageHeader from "../components/WeatherPageHeader.vue";
import WeatherPageBody from "../components/WeatherPageBody.vue";

const isLoading = ref<boolean>(true);
const shouldShowToast = ref<boolean>(false);
const cityName = ref<string>("");
const apiResult = ref<ApiResults | undefined>(undefined);
const weatherData = ref<WeatherData | undefined>(undefined);
const populationData = ref<PopulationData | undefined>(undefined);

const snackBarText = "Entered City Not Available";
const toastTimeout = 3000;

const searchWeatherData = async (city: string) => {
  if (city) {
    cityName.value = city;
    isLoading.value = true;
    const response = await axios.get(`${BackendApi.Url}${city}`);
    apiResult.value = response.data;
  }
};

const searchedRegion = computed(() =>
  weatherData.value
    ? `${weatherData.value.location.name}, ${weatherData.value.location.region}`
    : ""
);

watch(apiResult, (newValue: ApiResults | undefined) => {
  isLoading.value = false;

  if (
    newValue?.dataAvailability.weather ||
    newValue?.dataAvailability.population
  ) {
    weatherData.value = newValue?.weatherData;
    populationData.value = newValue?.populationData;
  } else {
    shouldShowToast.value = true;
  }
});

onMounted(() => searchWeatherData("ooty"));
</script>

<style lang="scss" scoped>
.weather-page {
  background-color: #f3f3f8;
}

.skeleton {
  padding: 20px;
}
</style>