<template>
  <div class="header">
    <div>
      <v-icon class="location-icon">{{ locationIcon }}</v-icon>
      <span>{{ props.cityName }}</span>
    </div>
    <input-box icon-src="mdi-magnify" @update-city-name="updateCityName" />
  </div>
</template>

<script setup lang="ts">
import { defineEmits, defineProps } from "vue";

import InputBox from "../components/InputBox.vue";

interface Props {
  cityName: string;
}

const emits = defineEmits<{ (e: "initiate-search", value: string): void }>();
const props = defineProps<Props>();

const locationIcon = "mdi-map-marker";

const updateCityName = (cityName: string) => emits("initiate-search", cityName);
</script>

