<template>
  <div class="additional-info">
    <highlight-card
      :title="populationText"
      :image-url="PeopleIcon"
      :highlight-values="populationCount"
      show-full-width
    />
    <highlight-card
      :title="sunRiseSetText"
      :highlight-values="sunRiseSet"
      show-full-width
      :image-url="SunIcon"
    />
  </div>
</template>

<script lang="ts" setup>
import { computed, defineProps } from "vue";

import {
  PopulationData,
  Forecast,
} from "../../weather-app-server/ApiResultType";

import PeopleIcon from "../assets/people.svg";
import SunIcon from "../assets/sun-solid.svg";
import HighlightCard from "./HighlightCard.vue";

interface Props {
  populationData: PopulationData;
  weatherData: Forecast;
}

const props = defineProps<Props>();

const populationText = "Population";
const sunRiseSetText = "Sun Rise & Set";

const populationCount = computed(() => {
  if (props.populationData?.data) {
    return [{ value: props.populationData.data.populationCounts[0].value }];
  }

  return [{ value: "No Data available" }];
});

const sunRiseSet = computed(() => {
  return [
    { value: props.weatherData.forecastday[0].astro.sunrise, unit: "Sun Rise" },
    { value: props.weatherData.forecastday[0].astro.sunset, unit: "Sun Set" },
  ];
});
</script>

<style lang="scss" scoped>
.additional-info {
  display: flex;
  flex-direction: column;
  row-gap: 10px;

  @media (max-width: 750px) {
    padding-top: 33px;
  }
}
</style>