<template>
  <div class="future-temparature-card">
    <image-container
      icon-size
      :image-url="props.weatherIcon"
      position-top-center
    />
    <p class="temparature">
      {{ props.temparature }}
      <span class="temparature__unit">{{ Units.Celcius }}</span>
    </p>
    <div>
      <span class="day">{{ day }}</span>
      <span class="date">{{ date }}</span>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { computed, defineProps, withDefaults } from "vue";

import { Units } from "../types/Enums";

import ImageContainer from "./ImageContainer.vue";

interface Props {
  date: string;
  temparature: number;
  weatherIcon?: string;
}

const props = withDefaults(defineProps<Props>(), {
  weatherIcon: "",
});

const days = [
  "Sunday",
  "Monday",
  "Tuesday",
  "Wednesday",
  "Thursday",
  "Friday",
  "Saturday",
];

const months = [
  "January",
  "February",
  "March",
  "Apri",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

const date = computed(() => {
  const date = new Date(props.date);
  const month = months[date.getMonth()];

  return `${month} ${date.getDate()}, ${date.getFullYear()}`;
});

const day = computed(() => {
  const date = new Date(props.date);

  return days[date.getDay()];
});
</script>

