<template>
  <v-text-field
    v-model.trim="cityName"
    class="input-field"
    solo
    hide-details
    :label="props.placeholder"
    :type="props.type"
    :append-icon="props.iconSrc"
    @keypress="(event) => handleInput(event)"
    @keyup.enter="updateCityName"
    @click:append="updateCityName"
  />
</template>

<script setup lang="ts">
import { defineEmits, defineProps, ref, withDefaults } from "vue";

const cityName = ref<string>("");

interface Props {
  iconPosition?: string;
  iconSrc?: string;
  placeholder?: string;
  type?: string;
}

const props = withDefaults(defineProps<Props>(), {
  iconPosition: "left",
  iconSrc: "",
  placeholder: "Enter Here",
  type: "text",
});

const emits = defineEmits<{ (e: "update-city-name", value: string): void }>();

const handleInput = (event: KeyboardEvent) => {
  const alphabetRegex = /^[a-zA-Z]$/;
  if (!alphabetRegex.test(event.key)) event.preventDefault();
};

const updateCityName = () => {
  emits("update-city-name", cityName.value);
};
</script>

<style lang="scss" scoped>
.input-field {
  display: flex;
  align-items: center;
  max-width: 350px;
}
</style>