<template>
  <div class="weather-details">
    <today-weather
      :current-weather="props.weatherData.current"
      :future-weather="props.weatherData.forecast"
      :population-count="populationCounts"
    />
    <weather-highlights :today-highlights="props.weatherData.current" />
    <additional-data
      :population-data="props.populationData"
      :weather-data="props.weatherData.forecast"
    />
  </div>
</template>

<script lang="ts" setup>
import { computed, defineProps } from "vue";

import {
  PopulationData,
  WeatherData,
} from "../../weather-app-server/ApiResultType";

import TodayWeather from "./TodayWeather.vue";
import WeatherHighlights from "./WeatherHighlights.vue";
import AdditionalData from "./AdditionalData.vue";

interface Props {
  populationData: PopulationData;
  weatherData: WeatherData;
}

const props = defineProps<Props>();

const populationCounts = computed(() =>
  props.populationData
    ? props.populationData.data.populationCounts[0].value
    : ""
);
</script>

